<template>
  <div contenteditable @input="$emit('input', $event.target.innerText)"></div>
</template>

<script>
export default {
  props: ['value'],
  mounted: function () {
    this.$el.innerText = this.value
  },
  watch: {
    content: function () {
      this.$el.innerText = this.value
    }
  }
}
</script>



